@charset "UTF-8";
/* ----HTML----*/
* {
  box-sizing: border-box;
}

/*----CLEARFIX----*/
.clearfix::after {
  content: " ";
  display: table;
  width: 100%;
  clear: both;
}

/* ----BODY----*/
.page {
  padding: 0;
  font-family: 16px;
  font-family: "OpenSansRegular";
  color: #424242;
  background-color: #ffffff;
}

/* ----CONTAINER---*/
.container {
  max-width: 1024px;
  margin: auto;
}

/*----Header----*/
.header__contact-info {
  /*Eсли не нужно использовать info-phone_decor-line_vertical*/
  /*(вертикальную линию возле номера телефона)*/
  /*Тогда снять комментарий с padding */
  /*и закомментировать селектор header__info-phone_decor-line_vertical*/
  /*padding: 7px 0 10px 0;*/
  text-align: right;
  background-color: #f0f2f4;
  box-shadow: 0px 2px 4px -2px #b9b9b9;
}

.header__info-phone {
  display: inline-block;
}

.header__info-phone_decor-line_vertical {
  border-right: 1px solid #d6d6d6;
  height: 40px;
  padding: 9px 9px 0 0;
}

.header__ico-phone {
  vertical-align: -3px;
  font-size: 24px;
}

.header__info-email {
  padding: 0 3px 0 18px;
  display: inline-block;
}

.header__ico-email {
  font-size: 19px;
}

.header__link {
  padding: 0 7px 0 10px;
  text-decoration: none;
  color: #424242;
  font-size: 15px;
  font-family: "HelveticaRegular";
  transition: 0.5s;
}

.header__link:hover {
  color: #dd2a24;
}

.header__info-social-net {
  display: inline-block;
  margin: 0 7px 0 0;
  border-radius: 12%;
  background-color: #1287bf;
}

.header__ico-social-net {
  font-size: 19px;
}

.header__link-social-net {
  display: block;
  padding: 4px 7px 4px 7px;
  width: 26px;
  height: 26px;
  text-decoration: none;
  color: #ffffff;
  transition: 0.5s;
}

.header__link-social-net:hover {
  color: #fbe29d;
}

.header__control {
  padding: 40px 0 40px 0;
}

/*logo*/
.logo {
  display: inline-block;
  width: 250px;
}

.logo__ico {
  margin: 0 5px 0 0;
  position: relative;
  left: -4px;
  font-size: 57px;
  color: #dd2a24;
  float: left;
}

.logo__heading {
  margin: 0;
  font-size: 37px;
  font-family: "HelveticaBold";
}

.logo__txt {
  margin: 0;
  font-size: 15px;
  font-family: "HelveticaRegular";
}

.container__navigation {
  margin: 0 0 0 176px;
}

.navigation {
  display: inline-block;
}

/*Menu*/
.menu {
  margin: 0;
  padding: 0;
}

.menu__item {
  list-style-type: none;
  display: inline-block;
  vertical-align: 21px;
}

.menu__link {
  text-decoration: none;
  text-transform: capitalize;
  font-family: "HelveticaBold";
  font-size: 15px;
  color: #424242;
  transition: 0.5s;
}

.menu__link:hover {
  color: #dd2a24;
}

.menu__ico {
  margin: 0 15px 0 15px;
  font-size: 9px;
}

.container__box-button {
  margin: 0 0 0 16px;
}

.box-button {
  display: inline-block;
  vertical-align: 20px;
}

/*Button*/
.btn {
  display: inline-block;
  padding: 10px 23px 10px 23px;
  border-radius: 5px;
  text-decoration: none;
  text-transform: capitalize;
  font-family: "HelveticaBold";
  font-size: 15px;
  color: #ffffff;
  transition: 0.5s;
}

.btn:hover {
  color: #fbe29d;
}

.btn_theme_bright {
  background-color: #dd2a24;
}

